import React, { useEffect, useState } from 'react'
import IFrame from 'components/activities/utils/IFrame'
import { useAtom } from 'jotai'
import { currentObjectAtom } from 'store/labs/oas'
import { Button, Divider, Header, Image, List, Segment } from 'semantic-ui-react'
import { RightFloatButton } from 'styles/App'
import { RouteChildrenProps } from 'react-router-dom'
import SpacedContainer from 'styles/common'
import { capitalize } from 'utils'

const learningBannerSrc = `${process.env.PUBLIC_URL}/assets/img/learining-banner.jpg`
const closeActivitySrc = `${process.env.PUBLIC_URL}/assets/img/close-activity.png`

const ObjectDetail = ({ history }: RouteChildrenProps): JSX.Element => {
  const [isObjectOpen, setIsObjectOpen] = useState(false)
  const [finished, setFinished] = useState(false)
  const [currentOA] = useAtom(currentObjectAtom)

  useEffect(() => {
    console.log('Object:', currentOA)
    // if (!currentOA.id) history.push('/a1')
  }, [history, currentOA])

  const openObject = () => setIsObjectOpen(true)

  const closeObject = () => {
    setIsObjectOpen(false)
    setFinished(true)
  }

  return (
    <div>
      {currentOA.path && isObjectOpen && (
        <Segment style={{ paddingTop: 0 }}>
          <RightFloatButton secondary icon="close" onClick={closeObject} />
          <IFrame src={currentOA.path} />
        </Segment>
      )}

      {!isObjectOpen && (
        <Segment>
          <SpacedContainer>
            <Header>
              {currentOA.level}: {capitalize(currentOA.type)} - {currentOA.title}
            </Header>
            <Image src={learningBannerSrc} size="medium" alt="banner" verticalAlign="middle" />
          </SpacedContainer>
          <Divider fitted />

          <Segment basic textAlign="center">
            <Segment.Group compact>
              <Segment color="teal" secondary>
                {!finished && (
                  <div>
                    <p>
                      When you finish the exercises, close the activity window to answer the
                      questionnaire that validates your work, as the example below.
                    </p>
                    <Image src={closeActivitySrc} size="huge" alt="close-activity" centered />
                  </div>
                )}

                {finished && (
                  <div>
                    <Header as="h5">Did you complete the activities?</Header>
                    <List as="ul">
                      <List.Item as="li">
                        Answer{' '}
                        <a href={currentOA.surveyUrl} target="_blank" rel="noopener noreferrer">
                          this questionnaire
                        </a>{' '}
                        to validate your work
                      </List.Item>
                    </List>
                  </div>
                )}
              </Segment>
              <Divider fitted />
              <Segment basic textAlign="left">
                {currentOA.id && (
                  <Button color="olive" onClick={openObject}>
                    {finished ? 'Start over!' : 'Begin!'}
                  </Button>
                )}
              </Segment>
            </Segment.Group>
          </Segment>
        </Segment>
      )}
    </div>
  )
}

export default ObjectDetail
